import FormTemplate from './form';
import AcHtmlElement from "AC/helpers/html-element";
export default class EditableTemplate {
    constructor(mode) {
        this.form = new FormTemplate();
        this.element = mode === 'bulk' ? createBulkTemplate() : createInlineTemplate();
        this.setError('');
    }
    setTemplate(template) {
        let inputs = this.getElement().querySelector('.aceditable__form__inputs');
        if (!inputs) {
            return this;
        }
        inputs.innerHTML = '';
        inputs.insertAdjacentHTML('afterbegin', template);
        return this;
    }
    getElement() {
        return this.element;
    }
    setError(error) {
        let error_element = this.getElement().querySelector('.aceditable__form__error');
        if (!error_element) {
            return this;
        }
        if (error) {
            error_element.innerHTML = error;
            error_element.style.display = 'block';
        }
        else {
            error_element.style.display = 'none';
        }
        return this;
    }
    getFormHelper() {
        return this.form;
    }
    addClass(className) {
        this.element.classList.add(className);
        return this;
    }
    setSubmitButton(label) {
        let submit = this.element.querySelector('[data-submit]');
        if (submit) {
            submit.innerHTML = label;
        }
    }
    showButtons(show = true) {
        let controls = this.element.querySelector('.aceditable__form__controls');
        if (!controls) {
            return this;
        }
        if (show) {
            controls.style.display = 'block';
        }
        else {
            controls.style.display = 'none';
        }
        return this;
    }
}
const createInlineTemplate = () => {
    return AcHtmlElement.create('div').addClasses('aceditable', '-inline').addHtml(`		
			<div class="aceditable__content">
				<form class="aceditable__form" autocomplete="off">
					<div class="aceditable__form__inputs">
						
					</div>
					<div class="aceditable__form__controls">
						<button data-submit="" class="button aceditable__button -primary"><span class="dashicons dashicons-yes"></span></button>
						<button data-cancel="" class="button aceditable__button"><span class="dashicons dashicons-no"></span></button>
					</div>
				</form>
				<div class="aceditable__form__error"></div>
			</div>
			<div class="aceditable__spinner spinner"></div>
		`).getElement();
};
const createBulkTemplate = () => {
    return AcHtmlElement.create('div').addClasses('aceditable', '-bulk').addHtml(`		
				<form class="aceditable__form" autocomplete="off">
					<div class="aceditable__form__inputs">			
					</div>
				</form>
		`).getElement();
};
