<script lang="ts">
    import {getEditingTranslation} from "ACP/Editing/helpers/translations";
    import {onMount} from "svelte";
    import AbstractEditable from "ACP/Editing/editable/abstract";
    import BulkEditProcessor from "ACP/Editing/bulk-edit/bulk-edit-processor";
    import BulkEditProcess from "ACP/Editing/bulk-edit/components/BulkEditProcess.svelte";
    import BulkLogDisplay from "ACP/Editing/bulk-edit/components/BulkLogDisplay.svelte";
    import BulkEditConfirmation from "ACP/Editing/bulk-edit/components/BulkEditConfirmation.svelte";
    import AcModal from "ACUi/AcModal.svelte";

    export let closeHandler: (d: boolean) => void;
    export let label: string;
    export let column: string;
    export let numberItems: string = ''
    export let editable: AbstractEditable;
    export let processor: BulkEditProcessor

	export let needConfirm: boolean = true;

    let canClose: boolean = true;
    let confirm = false;
    let confirmTriggerElement: HTMLElement;
    let running = false;
    let i18n = getEditingTranslation();
    let editableContainer: HTMLElement;

    const onClose = (deselect: boolean = false) => {
        cancel();
        canClose = true;
        closeHandler(deselect);
    }

    const cancel = () => {
        processor.cancel();
        running = false;
        canClose = true;
    }

    const save = () => {
        canClose = false;
        confirm = false;
        running = true;
        editable.disable();
        processor.save(column, editable.getValue());
    }

    const confirmSave = (e) => {
        if( ! needConfirm ){
            save();
            return;
		} else {
            confirm = true;
            canClose = false;
		}

    }

    const cancelConfirm = () => {
        confirm = false;
        canClose = true;
    }

    const checkClose = () => {
        if (canClose) {
            onClose();
        }
    }

    const processorFinished = () => {
        canClose = true;
    }

    onMount(() => {
        editableContainer.append(editable.get());
        editable.onSave((e) => {
            confirmSave(e);
        });
        setTimeout(() => editable.focus(), 100);
    });

    $: itemLabel = '1' == numberItems
        ? i18n.bulk_selection.item
        : 'all' == numberItems
            ? i18n.bulk_selection.all_items
            : i18n.bulk_selection.items.replace('{0}', numberItems);

    $: numberOfEntries = i18n.bulk_selection.affected_items.replace('{0}', '<strong>' + itemLabel + '</strong>');
</script>
<!-- svelte-ignore a11y-click-events-have-key-events -->
<AcModal visible className="-bulkedit" on:close={checkClose} disableClose={running}>
	<span slot="header">{@html label}</span>

	<svelte:fragment slot="content">
		<div data-editable="" bind:this={editableContainer}>

		</div>
	</svelte:fragment>

	<svelte:fragment slot="footer">
		{#if running }
			<BulkEditProcess processor={processor} on:finish={processorFinished} on:cancel={cancel} on:close={()=> onClose(false)} on:closeDeselect={ () => onClose(true)}></BulkEditProcess>
		{:else }
			<div class="ac-modal-footer-actions">
				<div class="ac-modal-footer-actions__info">
					<em>{@html numberOfEntries}</em>
				</div>
				<div class="ac-modal-footer-actions__buttons">
					<button class="button button-primary" on:click={confirmSave} bind:this={confirmTriggerElement}>{i18n.update}</button>
				</div>
			</div>
		{/if}

		<BulkLogDisplay logger={processor.getLogger()}></BulkLogDisplay>

		{#if confirm }
			<BulkEditConfirmation on:confirm={save} on:cancel={cancelConfirm} editable={editable} items={numberItems} triggerElement={confirmTriggerElement}></BulkEditConfirmation>
		{/if}
	</svelte:fragment>

</AcModal>