<script lang="ts">
    import {createEventDispatcher, onMount} from "svelte";
    import {getEditingTranslation} from "ACP/Editing/helpers/translations";
    import BulkDeleteProcessor from "ACP/Editing/bulk-edit/bulk-delete-processor";
    import AcProgressBar from "ACUi/AcProgressBar.svelte";
    import AcTimer from "ACUi/AcTimer.svelte";

    const dispatch = createEventDispatcher();

    export let processor: BulkDeleteProcessor;

    let error = false;
    let errorMsg = '';
    let finished = false;
    let cancelled = false;
    let i18n = getEditingTranslation();
    let startTimer: () => void;
    let stopTimer: () => void;
    let toProcess = 0;
    let processed = 0;
    let percentage = 0;

    const cancel = () => {
        stopTimer();
        finished = true;
        dispatch('cancel');
    }
    const close = () => dispatch('close');

    onMount(() => {
        startTimer();

        const events = processor.events;

        events.onUpdateCount(count => toProcess = count);
        events.onFinish(() => {
            finished = true;
            stopTimer();
        });
        events.onUpdate((d, ids) => {
            processed = toProcess - ids.length;
            percentage = Math.ceil((processed / toProcess) * 100);
        });
        events.onError(d => {
            stopTimer();
            error = true;
            errorMsg = d;
            finished = true;
            percentage = 100;
        })
    });

    $: processedFormatted = new Intl.NumberFormat().format(processed);
    $: toProcessFormatted = new Intl.NumberFormat().format(toProcess);
    $: processedString = `${processedFormatted} / ${toProcessFormatted}`;
    $: processedFullString = i18n.bulk_delete.finished.replace('{0}', processedString);
</script>


<div class="acu-flex acu-flex-row-reverse">
	<div class="">
		<AcTimer bind:start={startTimer} bind:stop={stopTimer}></AcTimer>
	</div>

	<div class="acu-grow">
		{#if error}
			{i18n.failed} <span class="dashicons dashicons-no acu-text-notification-red"></span>
		{:else if !finished}
			{processedFullString}
		{:else }
			{processedFullString} (100%)
			<span class="dashicons dashicons-yes"></span>
		{/if}
	</div>

</div>
<AcProgressBar {percentage} {finished} {error}></AcProgressBar>
{#if error}
	<div class="acu-mb-3">
		<em>{errorMsg}</em>
	</div>
{/if}
<div>
	{#if finished || cancelled}
		<button class="button button-primary" on:click={close}>{i18n.done}</button>
	{:else }
		<button class="button button-primary" on:click={cancel}>{i18n.cancel}</button>
	{/if}
</div>
