import axios from "axios";
import { AJAX_VARS } from "../const";
import { mapDataToFormData } from "AC/helpers/global";
import BulkProcessorEvents from "ACP/Editing/bulk-edit/bulk-processor-events";
export default class BulkDeleteProcessor {
    constructor(BulkSelection, perIteration, logger) {
        this.BulkSelection = BulkSelection;
        this.perIteration = perIteration;
        this.logger = logger;
        this.events = new BulkProcessorEvents();
        this.ids = [];
        this.AbortController = new AbortController();
        this.BulkSelection.onUpdated((ids) => {
            this.events.emit('updateCount', ids.length);
        });
    }
    setArguments(args) {
        this.args = args;
        return this;
    }
    getBulkSelection() {
        return this.BulkSelection;
    }
    getLogger() {
        return this.logger;
    }
    start() {
        this.BulkSelection.getIDs().then(d => {
            this.ids = d;
            this.events.emit('updateCount', this.ids.length);
            this.startThread();
        });
        return this;
    }
    cancel() {
        this.BulkSelection.cancel();
        this.AbortController.abort();
        this.AbortController = new AbortController();
    }
    startThread() {
        this.events.emit('startThread');
        let ids = this.ids.splice(0, this.perIteration);
        if (ids.length === 0) {
            this.events.emit('finished');
            return;
        }
        axios.post(ajaxurl, mapDataToFormData({
            action: AJAX_VARS.ACTION,
            method: AJAX_VARS.METHOD_BULK_DELETE,
            layout: AC.layout,
            ids: ids,
            arguments: this.args,
            _ajax_nonce: AC.ajax_nonce
        }), {
            signal: this.AbortController.signal
        }).then((response) => {
            switch (response.data.success) {
                case true:
                    this.events.update(ids, this.ids);
                    this.logger.add(response.data.data.results);
                    this.startThread();
                    break;
                case false:
                    this.cancel();
                    this.events.emitError(response.data.data.message);
            }
        }).catch((reason) => {
            this.cancel();
            this.events.emitError(reason.message);
        });
    }
}
