import BulkDeleteModal from "ACP/Editing/bulk-edit/components/BulkDeleteModal.svelte";
import AcHtmlElement from "AC/helpers/html-element";
import DeleteUserArguments from "ACP/Editing/bulk-edit/components/delete-arguments/DeleteUserArguments.svelte";
import DeletePostArguments from "ACP/Editing/bulk-edit/components/delete-arguments/DeletePostArguments.svelte";
import DeleteDefaultArguments from "ACP/Editing/bulk-edit/components/delete-arguments/DeleteDefaultArguments.svelte";
import DeleteTrashArguments from "ACP/Editing/bulk-edit/components/delete-arguments/DeleteTrashArguments.svelte";
export default class BulkDelete {
    constructor(BulkActionRow, Processor, modalComponent) {
        this.BulkActionRow = BulkActionRow;
        this.Processor = Processor;
        this.modalComponent = modalComponent;
        this.init();
    }
    init() {
        let link = AcHtmlElement.create('a').addClass('ac-link').addHtml('<span class="dashicons dashicons-trash"></span>').addEventListener('click', () => this.startBulkDelete());
        this.BulkActionRow.getRow().getElement().querySelectorAll('.column-cb').forEach(el => el.appendChild(link.getElement()));
    }
    destroyComponent() {
        if (null !== this.componentContainer) {
            this.component.$destroy();
            this.componentContainer.remove();
        }
    }
    getModalComponent() {
        switch (this.modalComponent) {
            case 'user':
                return DeleteUserArguments;
            case 'post':
            case 'comment':
                return DeletePostArguments;
            case 'trash':
                return DeleteTrashArguments;
            default:
                return DeleteDefaultArguments;
        }
    }
    startBulkDelete() {
        this.componentContainer = document.createElement('div');
        document.body.appendChild(this.componentContainer);
        this.component = new BulkDeleteModal({
            target: this.componentContainer,
            props: {
                argumentComponent: this.getModalComponent(),
                Processor: this.Processor,
                numberItems: this.Processor.getBulkSelection().isPaginatedSelected() ? 'all' : this.Processor.getBulkSelection().getCount(),
                closeHandler: (reload = false) => {
                    this.destroyComponent();
                    if (reload) {
                        location.reload();
                    }
                }
            }
        });
    }
}
