<script lang="ts">
    import {getEditingTranslation} from "ACP/Editing/helpers/translations";
    import {createEventDispatcher, onMount, onDestroy} from "svelte";
    import AbstractEditable from "ACP/Editing/editable/abstract";

    const dispatch = createEventDispatcher();

    export let triggerElement: HTMLElement;
    export let items: string = '';
    export let editable: AbstractEditable

    let i18n = getEditingTranslation();
    const confirm = () => dispatch('confirm');
    const cancel = () => dispatch('cancel');
    let y = 0;
    let x = 0;


    const keyPressBinding = (e: KeyboardEvent) => {
        if (e.key === 'Enter') {
            confirm();
        }
    }

    onMount(() => {
        if( ! triggerElement ){
            triggerElement = editable.getEditableTemplate().getElement().querySelector('button[data-submit]');
		}

        let box = triggerElement.getBoundingClientRect();

        y = Math.floor(box.y + box.height);
        x = Math.floor(box.x + (box.width / 2));

        document.addEventListener('keypress', keyPressBinding);
    });

    onDestroy(() => {
        document.removeEventListener('keypress', keyPressBinding);
    })

    $: selectionString = i18n.bulk_edit.form.update_values.replace('{0}', items);
</script>
<!-- svelte-ignore a11y-click-events-have-key-events -->
<div class="acp-be-confirm -v2" style="position:fixed; top:{y}px; left: {x}px; transform:translateX(-50%)" on:click|stopPropagation role="none">
	<span class="acp-be-confirm__caret"></span>
	<em class="acp-be-confirm__info">{selectionString}</em>
	<div class="acp-be-confirm__controls">
		<strong>{i18n.bulk_edit.form.are_you_sure}</strong>
		<span class="acp-be-confirm__controls__link -confirm" on:click={confirm} role="button" tabindex="0">{i18n.bulk_edit.form.yes_update}</span>
		<span class="acp-be-confirm__controls__link -cancel" on:click|stopPropagation={cancel} role="button" tabindex="0">{i18n.cancel}</span>
	</div>
</div>