<script lang="ts">
    import {getEditingTranslation} from "ACP/Editing/helpers/translations";
    import {createEventDispatcher, onDestroy, onMount} from "svelte";

    const dispatch = createEventDispatcher();

    export let target: HTMLElement
    export let mode: string = '';

    let i18n = getEditingTranslation();
    const confirm = () => dispatch('confirm');
    const cancel = () => dispatch('cancel');
    let y = 0;
    let x = 0;

    const keyPressBinding = (e: KeyboardEvent) => {
        if (e.key === 'Enter') {
            confirm();
        }
    }

    onMount(() => {
        let box = target.getBoundingClientRect();

        y = Math.floor(box.y + box.height);
        x = Math.floor(box.x + (box.width / 2));

        document.addEventListener('keypress', keyPressBinding);
    });

    onDestroy(() => {
        document.removeEventListener('keypress', keyPressBinding);
    });

    $:i18n_confirmation = mode === 'restore' ? i18n.bulk_delete.confirmation_restore : i18n.bulk_delete.confirmation;
    $:i18n_bulk_yes = mode === 'restore' ? i18n.bulk_delete.yes_restore : i18n.bulk_delete.yes_delete;
</script>
<!-- svelte-ignore a11y-click-events-have-key-events -->
<div class="acp-be-confirm -v2 -delete" style="position:fixed; top:{y}px; left: {x}px; transform:translateX(-50%)">
	<span class="acp-be-confirm__caret"></span>
	<em class="acp-be-confirm__info">{i18n_confirmation}</em>
	<div class="acp-be-confirm__controls">
		<span class="acp-be-confirm__controls__link -confirm" on:click={confirm} role="button" tabindex="0">{i18n_bulk_yes}</span>
		<span class="acp-be-confirm__controls__link -cancel" on:click={cancel} role="button" tabindex="0">{i18n.cancel}</span></div>
</div>