import Editables from 'ACP/Editing/modules/editables';
import Helper from 'ACP/Editing/modules/helper';
import InlineEdit from 'ACP/Editing/inline-edit/inline-edit';
import { mapColumns } from 'ACP/Editing/middleware/columns';
import { CellMiddleware } from 'ACP/Editing/middleware/cells';
import TableUpdate from 'ACP/Editing/modules/table-update';
import { getEditableValues } from "ACP/Editing/helpers/ajax";
import EditableMiddleware from "ACP/Editing/middleware/editable";
import EditingModule from "ACP/Editing/modules/main-module";
import BulkDeleteProcessor from "ACP/Editing/bulk-edit/bulk-delete-processor";
import BulkDelete from "ACP/Editing/bulk-edit/bulk-delete";
import BulkLogger from "ACP/Editing/bulk-edit/bulk-logger";
import { BulkSelection, PaginatedBulkSelector } from "ACP/Editing/bulk-edit/bulk-selection";
import ObjectIdFetcher from "ACP/Editing/bulk-edit/object-id-fetcher";
import BulkSelectionRow from "ACP/Editing/bulk-edit/bulk-selection-row";
import BulkActionRow from "ACP/Editing/bulk-edit/bulk-action-row";
import BulkEdit from "ACP/Editing/bulk-edit/bulk-edit";
import BulkEditProcessor from "ACP/Editing/bulk-edit/bulk-edit-processor";
AC_SERVICES.addListener('Table.Ready', (e) => {
    const Table = e.table;
    if (!AC.hasOwnProperty('layout') || null === AC.layout) {
        return;
    }
    mapColumns(e.table.Columns, ACP_Editing_Columns);
    const Editing = new EditingModule();
    const EditablesService = new Editables(AC_SERVICES);
    const EditableMiddlewareService = new EditableMiddleware(EditablesService, AC_SERVICES);
    const TableUpdateService = new TableUpdate(Table);
    const BulkSelectionService = new BulkSelection(Table.Selection);
    const BulkSelectionRowService = new BulkSelectionRow(BulkSelectionService, Table);
    const BulkActionRowService = new BulkActionRow(Table, BulkSelectionRowService);
    AC_SERVICES.registerService('Editing', Editing);
    Editing
        .registerService('Editables', EditablesService)
        .registerService('Helper', new Helper())
        .registerService('TableUpdate', TableUpdateService)
        .registerService('BulkSelectionRow', BulkSelectionRowService)
        .registerService('BulkSelection', BulkSelectionService);
    if (ACP_Editing.bulk_delete.active) {
        BulkSelectionRowService.setActive(true);
        const BulkDeleteService = new BulkDelete(BulkActionRowService, new BulkDeleteProcessor(new PaginatedBulkSelector(BulkSelectionService, new ObjectIdFetcher('bulk-deletable-rows')), ACP_Editing.bulk_delete.delete_rows_per_iteration, new BulkLogger()), ACP_Editing.bulk_delete.component);
        Editing.registerService('BulkDelete', BulkDeleteService);
        AC_SERVICES.emitEvent('Service.Registered.Editing.BulkDelete', BulkDeleteService);
    }
    if (ACP_Editing.bulk_edit.active) {
        BulkSelectionRowService.setActive(true);
        const BulkEditService = new BulkEdit(Table, BulkSelectionService, BulkActionRowService, EditableMiddlewareService, new BulkEditProcessor(ACP_Editing.bulk_edit.updated_rows_per_iteration, new PaginatedBulkSelector(BulkSelectionService, new ObjectIdFetcher('bulk-editable-rows')), new BulkLogger()), new TableUpdate(Table));
        Editing.registerService('BulkEdit', BulkEditService);
        AC_SERVICES.emitEvent('Service.Registered.Editing.BulkEdit', BulkEditService);
    }
    let itemIds = Table.getIdsFromTable();
    if (ACP_Editing.inline_edit.active && itemIds.length > 0) {
        let Editing_items = getEditableValues(Table.getIdsFromTable());
        Editing_items.then((response) => {
            if (response.data.success) {
                CellMiddleware(Table.Cells, response.data.data.editable_values);
                const InlineEditService = new InlineEdit(Table, EditablesService, EditableMiddlewareService, ACP_Editing.inline_edit.toggle_state || ACP_Editing.inline_edit.persistent, ACP_Editing.inline_edit.version);
                Editing.registerService('InlineEdit', InlineEditService);
                AC_SERVICES.emitEvent('Service.Registered.Editing.InlineEdit', InlineEditService);
            }
        });
    }
    else {
        document.querySelectorAll('.ac-table-button.-toggle.-iedit').forEach(el => el.remove());
    }
    const reinitTooltips = () => {
        setTimeout(() => {
            AC_SERVICES.hasService('tooltips') ? AC_SERVICES.getService('tooltips')() : null;
        });
    };
    AC_SERVICES.addListener('Edit.Inline.setValue', reinitTooltips);
    BulkSelectionRowService.events.onShow(reinitTooltips);
    BulkSelectionRowService.events.onUpdate(reinitTooltips);
});
