<script lang="ts">
    import {getEditingTranslation} from "ACP/Editing/helpers/translations";
    import BulkLogDisplay from "ACP/Editing/bulk-edit/components/BulkLogDisplay.svelte";
    import BulkDeleteProcessor from "ACP/Editing/bulk-edit/bulk-delete-processor";
    import BulkDeleteProcess from "ACP/Editing/bulk-edit/components/BulkDeleteProcess.svelte";
    import BulkDeleteConfirmation from "ACP/Editing/bulk-edit/components/BulkDeleteConfirmation.svelte";
    import AcModal from "ACUi/AcModal.svelte";

    export let Processor: BulkDeleteProcessor;
    export let closeHandler: (reload: boolean) => void;
    export let numberItems: string = '1';
    export let argumentComponent = null;

    let i18n = getEditingTranslation();
    let labelDelete = i18n.delete
    let visible = true;
    let running = false;
    let confirm = false;
    let deleteArguments = {};
    let deleteButton;
    let canClose: boolean = true;
    let cancelled: boolean;

    const Abort = () => {
        Processor.cancel();
        canClose = true;
        cancelled = true;
    }

    const confirmation = () => {
        confirm = true;
    }

    const startBulkDelete = () => {
        canClose = false;
        running = true;
        confirm = false;
        Processor.setArguments(deleteArguments).start();
    }

    const close = () => {
        if (!canClose) {
            return false;
        }

        onClose();
    }

    const onClose = (reload: boolean = false) => {
        Processor.cancel();
        if (typeof closeHandler === 'function') {
            closeHandler(reload);
        }
    }

    $: itemLabel = '1' == numberItems
        ? i18n.bulk_selection.item
        : 'all' == numberItems
            ? i18n.bulk_selection.all_items
            : i18n.bulk_selection.items.replace('{0}', numberItems);

    $: numberOfEntries = i18n.bulk_selection.affected_items.replace('{0}', '<strong>' + itemLabel + '</strong>');

    $: mode = labelDelete === i18n.bulk_delete.restore ? 'restore' : 'delete';

    $: i18n_modal_label = labelDelete === i18n.bulk_delete.restore ? i18n.bulk_delete.bulk_restore : i18n.bulk_delete.bulk_delete;
</script>
<AcModal visible disableScroll
		className="-bulkdelete"
		on:close={close}
		disableClose={!canClose}
		hideContent={running}
>
	<span slot="header">{i18n_modal_label}</span>

	<svelte:fragment slot="content">
		{#if !running }
			<div>
				<svelte:component this={argumentComponent} bind:args={deleteArguments} bind:buttonLabel={labelDelete}/>
			</div>
		{/if}
	</svelte:fragment>

	<svelte:fragment slot="footer">
		{#if running }
			<BulkDeleteProcess processor={Processor} on:cancel={Abort} on:close={() => onClose( true )}/>
		{:else }
			<div class="ac-modal-footer-actions">
				<div class="ac-modal-footer-actions__info">
					<em>{@html numberOfEntries}</em>
				</div>
				<div class="ac-modal-footer-actions__buttons">
					<button bind:this={deleteButton} class="button button-primary" on:click={confirmation}>{labelDelete}</button>
				</div>
			</div>
		{/if}

		<BulkLogDisplay logger={Processor.getLogger()}></BulkLogDisplay>

		{#if confirm }
			<BulkDeleteConfirmation target={deleteButton} on:confirm={startBulkDelete} on:cancel={()=> confirm=false} mode={mode}></BulkDeleteConfirmation>
		{/if}
	</svelte:fragment>

</AcModal>