<script lang="ts">
    import {onMount} from "svelte";
    import axios from "axios";
    import {getEditingTranslation} from "ACP/Editing/helpers/translations";

    declare const ajaxurl: string;
    declare const pwsL10n: any;
    declare const wp: any;

    export let value: string;
    export let onChange: Function;
    export let show: boolean = true;
    export let placeholder: string;

    const i18n = getEditingTranslation();

    let rating: number = 0;
    let ratingTxt: string = '';
    let ratingCls: string = '';

    const emitChange = () => {
        if (onChange) {
            onChange(value);
        }
    }

    const handleToggleClick = (e) => {
        show = !show;
    }

    const onInput = (e: KeyboardEvent) => {
        value = e.target.value;

        if (['Enter'].indexOf(e.key) !== -1) {
            //return;
        }

        updateRating();

        emitChange();
    }

    const generatePassword = () => {
        const data = new FormData();
        data.set('action', 'generate-password');

        axios.post(ajaxurl, data).then(r => {
            if (r.data.success) {
                value = r.data.data;
                updateRating();
            }
        })
    }

    const updateRating = () => {
        rating = wp.passwordStrength.meter(value, {}, value);

        switch (rating) {
            case 2:
                ratingTxt = pwsL10n.bad;
                ratingCls = '-bad';
                break;
            case 3:
                ratingTxt = pwsL10n.good;
                ratingCls = '-good';
                break;
            case 4:
                ratingTxt = pwsL10n.strong;
                ratingCls = '-strong';
                break;
            case 5:
                ratingTxt = pwsL10n.mismatch;
                break;
            default:
                ratingTxt = pwsL10n.short;
                ratingCls = '-short';
        }
    }

    const init = (el: HTMLElement) => {
        setTimeout(() => {
            el.focus()
        }, 200);
    }

    onMount(() => {
        if (typeof value !== 'string') {
            value = '';
        }

        updateRating();
    })

    $: type = show ? 'text' : 'password';

</script>
<style>
	button.button {
		white-space: nowrap;
		width: 120px;
	}

	button .dashicons {
		transform: translateY(3px);
	}

	input {
		width: 300px !important;
		border-radius: 3px;
	}

	.acp-password-strength {
		margin-top: 5px;
		padding: 6px 0;
		width: 298px;
		text-align: center;
		font-weight: bold;
		margin-bottom: 10px;
		background: var(--ac-color-error-alt);
		border: 1px solid var(--ac-color-error);
	}

	.acp-password-strength.-good {
		background: #f5e6ab;
		border-color: #f0c33c;
	}

	.acp-password-strength.-strong {
		background: #b8e6bf;
		border-color: #68de7c;
	}

	button .dashicons-image-rotate {
		font-size: 16px;
		transform: translateY(5px);
	}
</style>
<!-- svelte-ignore a11y-click-events-have-key-events -->
<div style="display: flex;">
	<input {value} on:keyup|stopPropagation={onInput} {type} use:init {placeholder} required/>
	<button type="button" class="button" on:click|preventDefault|stopPropagation={handleToggleClick}>
		{#if show}
			<span class="dashicons dashicons-hidden"></span> {i18n.hide}
		{:else}
			<span class="dashicons dashicons-visibility"></span> {i18n.show}
		{/if}
	</button>
</div>
<div class="acp-password-strength {ratingCls}">{ratingTxt}</div>
<button type="button" class="button" on:click|preventDefault|stopPropagation={ generatePassword }>
	<span class="dashicons dashicons-image-rotate"></span> {i18n.generate}
</button>
