import Nanobus from "nanobus";
export class BulkSelection {
    constructor(RowSelection) {
        this.RowSelection = RowSelection;
        this.paginatedSelected = false;
        this.events = new Nanobus();
        this.getCheckBoxes().forEach(el => {
            el.addEventListener('input', () => {
                this.setPaginatedSelected(false);
                this.triggerChange();
            });
        });
    }
    getCheckBoxes() {
        return this.RowSelection.Table.getElement().querySelectorAll('#cb input[type=checkbox], th.check-column input[type=checkbox]');
    }
    deselectAll() {
        this.getCheckBoxes().forEach(el => el.checked = false);
        this.events.emit('selectionChanged');
    }
    triggerChange() {
        this.events.emit('selectionChanged');
    }
    onUpdated(fn) {
        this.events.on('selectionChanged', fn);
    }
    getIDs() {
        return this.RowSelection.getIDs();
    }
    getCount() {
        return this.RowSelection.getCount();
    }
    isAllSelected() {
        return this.RowSelection.isAllSelected();
    }
    setPaginatedSelected(value) {
        this.paginatedSelected = value;
        this.events.emit('selectionChanged');
        return this;
    }
    isPaginatedSelected() {
        return this.paginatedSelected;
    }
}
export class PaginatedBulkSelector {
    constructor(BulkSelection, ObjectIdFetcher) {
        this.BulkSelection = BulkSelection;
        this.ObjectIdFetcher = ObjectIdFetcher;
    }
    getIDs() {
        return new Promise((resolve, reject) => {
            if (this.isPaginatedSelected()) {
                this.ObjectIdFetcher.getIds().then(d => resolve(d)).catch(e => reject(e));
            }
            else {
                resolve(this.BulkSelection.getIDs());
            }
        });
    }
    deselectAll() {
        this.BulkSelection.deselectAll();
    }
    cancel() {
        this.ObjectIdFetcher.cancel();
    }
    onUpdated(fn) {
        this.ObjectIdFetcher.onUpdated(fn);
    }
    getCount() {
        return this.BulkSelection.getCount();
    }
    isAllSelected() {
        return this.BulkSelection.isAllSelected();
    }
    setPaginatedSelected(value) {
        this.BulkSelection.setPaginatedSelected(value);
    }
    isPaginatedSelected() {
        return this.BulkSelection.isPaginatedSelected();
    }
}
