<script lang="ts">

    import {onMount} from "svelte";
    import {getEditingTranslation} from "ACP/Editing/helpers/translations";

    export let args: { restore: boolean, force_delete: boolean } = {};
    export let buttonLabel;

    let i18n = getEditingTranslation();
    let method = 'purge';

    const checkValue = () => {
        if (method === 'purge') {
            args.force_delete = true;
            args.restore = false;
            buttonLabel = i18n.delete
        } else {
            args.force_delete = false;
            args.restore = true;
            buttonLabel = i18n.bulk_delete.restore
        }
    }

    onMount(() => {
        args = {
            force_delete: true,
            restore: false
        }

        checkValue();
    });

</script>

<div>
	<div style="margin-bottom: 10px;">
		<label><input bind:group={method} type="radio" value="restore" on:change={checkValue}/> {i18n.bulk_delete.restore_items}
		</label>
	</div>
	<div>
		<label><input bind:group={method} type="radio" value="purge" on:change={checkValue}/> {i18n.bulk_delete.delete_items_permanently}
		</label>
	</div>
</div>