<script lang="ts">
    import {getEditingTranslation} from "ACP/Editing/helpers/translations";
    import {onMount} from "svelte";
    import {BulkSelection as BulkSelectionService} from "ACP/Editing/bulk-edit/bulk-selection";

    export let bulkSelection: BulkSelectionService;
    export let totalItems = 0;
    export let totalItemsFormatted: string;

    export const setTotalItems = (items: number, itemsFormatted: string) => {
        totalItems = items;
        totalItemsFormatted = itemsFormatted;
    }

    let i18n = getEditingTranslation();
    let selected = '0';
    let showAllSelector = false;

    const selectPaginated = () => {
        bulkSelection.setPaginatedSelected(true);
        selected = 'all';
    }

    onMount(() => {
        bulkSelection.onUpdated(() => {
            selected = bulkSelection.getCount().toString();
            showAllSelector = totalItems > bulkSelection.getCount() && bulkSelection.isAllSelected() && !bulkSelection.isPaginatedSelected();
        });
    })

    $: itemsString = '1' == selected
        ? i18n.bulk_selection.item
        : 'all' == selected
            ? i18n.bulk_selection.all_items_ucfirst
            : i18n.bulk_selection.items.replace('{0}', selected);

    $: selectionString = i18n.bulk_selection.selected.replace('{0}', '<strong>' + itemsString + '</strong>');

    $: selectAll = i18n.bulk_selection.select_all.replace('{0}', totalItemsFormatted);
</script>
<!-- svelte-ignore a11y-click-events-have-key-events -->
<div class="acp-ba-row__notice">
	{@html selectionString}
	{#if showAllSelector}
		<span class="ac-link" on:click={selectPaginated} role="button" tabindex="0">{selectAll}</span>
	{/if}
</div>