<script lang="ts">
    import {onMount} from "svelte";
    import BulkLogger from "ACP/Editing/bulk-edit/bulk-logger";
    import {getEditingTranslation} from "ACP/Editing/helpers/translations";

    export let logger: BulkLogger;
    let i18n = getEditingTranslation();
    let errors: any[] = [];
    let errorsCount = 0;
    let errorVisible = false;
    let notEditables = [];
    let notEditablesCount = 0;
    let notEditablesVisible = false;

    onMount(() => {
        logger.onUpdated(() => {
            errors = logger.getErrors();
            errorsCount = errors.length;
            notEditables = logger.getNotEditable();
            notEditablesCount = notEditables.length;
        });
    });

    $: errorString = i18n.bulk_edit.feedback.error.replace('{0}', `<span class="ac-bulk-log__status__count">${errorsCount} errors </span>`);
    $: errorToggleString = errorVisible ? i18n.bulk_edit.feedback.hide_items : i18n.bulk_edit.feedback.show_items
    $: uneditableString = i18n.bulk_edit.feedback.error.replace('{0}', `<span class="ac-bulk-log__status__count -medium">${notEditablesCount} unmodifiable items</span>`);
    $: uneditableToggleString = notEditablesVisible ? i18n.bulk_edit.feedback.hide_items : i18n.bulk_edit.feedback.show_items
</script>
<!-- svelte-ignore a11y-click-events-have-key-events -->
<div class:-active={errors.length>0 || notEditables.length>0}>
	{#if errors.length > 0}
		<div class="ac-bulk-log">
			<div class="ac-bulk-log__status">
				<span>{@html errorString}</span>
				<span class="ac-bulk-log-link" on:click={() => errorVisible = !errorVisible} role="button" tabindex="0">{errorToggleString}</span>
			</div>
			<div class="ac-bulk-log__items" class:-visible={errorVisible}>
				{#each errors as error}
					<div>ID: {error.id} - {error.error}</div>
				{/each}
			</div>
		</div>
	{/if}

	{#if notEditables.length > 0}
		<div class="ac-bulk-log">
			<div class="ac-bulk-log__status">
				<span>{@html uneditableString}</span>
				<span class="ac-bulk-log-link" on:click={() => notEditablesVisible = !notEditablesVisible} role="button" tabindex="0">{uneditableToggleString}</span>
			</div>
			<div class="ac-bulk-log__items" class:-visible={notEditablesVisible}>
				{#each notEditables as error}
					<div>ID: {error.id} - {error.error}</div>
				{/each}
			</div>
		</div>
	{/if}

</div>