import Nanobus from "nanobus";
export default class BulkLogger {
    constructor() {
        this.clear();
        this.events = new Nanobus();
    }
    getErrors() {
        return this.data.filter(r => r.status === 'failed');
    }
    getInvalid() {
        return this.data.filter(r => r.status === 'not_editable');
    }
    getNotEditable() {
        return this.data.filter(r => r.status === 'not_editable');
    }
    hasError() {
        return this.getErrors().length > 0;
    }
    hasNotEditable() {
        return this.getNotEditable().length > 0;
    }
    onUpdated(fn) {
        this.events.addListener('updated', fn);
    }
    add(data) {
        this.data = [...this.data, ...data];
        this.events.emit('updated', data);
    }
    clear() {
        this.data = [];
        return this;
    }
}
