export const escapeRegex = (value) => {
    return value.replace(/[\-\[\]\/\{\}\(\)\*\+\?\.\\\^\$\|]/g, "\\$&");
};
export const formatOptionsSelect2 = (options) => {
    let foptions = [];
    if (typeof options === "undefined") {
        return foptions;
    }
    for (let i = 0; i < options.length; i++) {
        let parent;
        if (typeof options[i].options !== 'undefined') {
            parent = {
                text: options[i].label,
                children: []
            };
            for (let j in options[i].options) {
                if (options[i].options.hasOwnProperty(j)) {
                    parent.children.push({
                        value: options[i].options[j].value,
                        id: options[i].options[j].value,
                        text: options[i].options[j].label
                    });
                }
            }
        }
        else {
            parent = {
                value: options[i].value,
                id: options[i].value,
                text: options[i].label
            };
        }
        foptions.push(parent);
    }
    return foptions;
};
/**
 * Deprecated helper API for third party addons
 */
export default class Helper {
    escapeRegex(value) {
        return escapeRegex(value);
    }
}
