import AbstractEditable from './abstract';
import Password from "./components/Password.svelte";
export default class PasswordEditable extends AbstractEditable {
    constructor(args) {
        super(args);
        this.dynamicValue = '';
    }
    getEditableType() {
        return "wp_password";
    }
    valueToInput(value) {
        this.dynamicValue = value;
        let container = this.getElement().querySelector('[data-component]');
        new Password({
            target: container,
            props: {
                value: value,
                placeholder: this.settings.placeholder,
                onChange: (value) => {
                    this.dynamicValue = value;
                }
            }
        });
    }
    getValue() {
        return this.dynamicValue;
    }
    getTemplate() {
        return `
            <div data-component></div>
        `;
    }
}
