export const getParamFromUrl = (param, url) => {
    if (!url.includes('?')) {
        return null;
    }
    const params = new URLSearchParams(url.split('?')[1]);
    return params.get(param);
};
export const mapDataToFormData = (data, formData = null) => {
    let fData = formData !== null && formData !== void 0 ? formData : new FormData();
    Object.keys(data).forEach(key => {
        appendObjectToFormData(fData, data[key], key);
    });
    return fData;
};
export const appendObjectToFormData = (formData, data, parentKey = '') => {
    if (data && typeof data === 'object' && !(data instanceof Date) && !(data instanceof File)) {
        Object.keys(data).forEach(key => {
            appendObjectToFormData(formData, data[key], parentKey ? `${parentKey}[${key}]` : key);
        });
    }
    else {
        const value = data == null ? '' : data;
        formData.append(parentKey, value);
    }
};
export const sanitizeColumnSelector = (name) => {
    return name.replace(/\./g, '\\.');
};
