import axios from "axios";
import Nanobus from "nanobus";
export default class ObjectIdFetcher {
    constructor(requestMethod) {
        this.requestMethod = requestMethod;
        this.ids = [];
        this.events = new Nanobus();
        this.AbortController = new AbortController();
    }
    cancel() {
        this.AbortController.abort();
        this.AbortController = new AbortController();
    }
    getIds() {
        return new Promise((resolve) => {
            this.events.once('ready', () => {
                resolve(this.ids);
            });
            this.retrieveItems(1);
        });
    }
    onUpdated(fn) {
        this.events.addListener('updated', fn);
    }
    retrieveItems(page) {
        axios.get(window.location.href, {
            params: {
                method: this.requestMethod,
                list_screen: AC.list_screen,
                layout: AC.layout,
                ac_page: page,
                _ajax_nonce: AC.ajax_nonce
            },
            signal: this.AbortController.signal
        }).then((r) => {
            if (r.data.success) {
                this.ids = [...this.ids, ...r.data.data.ids];
                this.events.emit('updated', this.ids);
                if (r.data.data.rows_per_iteration === r.data.data.ids.length) {
                    this.retrieveItems(page + 1);
                }
                else {
                    this.events.emit('ready');
                }
            }
        }).catch(() => {
        });
    }
}
