<script lang="ts">
    import {createEventDispatcher, onMount} from "svelte";
    import {getEditingTranslation} from "ACP/Editing/helpers/translations";
    import BulkEditProcessor from "ACP/Editing/bulk-edit/bulk-edit-processor";
    import AcProgressBar from "ACUi/AcProgressBar.svelte";
    import AcTimer from "ACUi/AcTimer.svelte";

    const dispatch = createEventDispatcher();

    export let processor: BulkEditProcessor;

    let error = false;
    let errorMessage = '';
    let finished = false;
    let cancelled = false;
    let i18n = getEditingTranslation();
    let startTimer;
    let stopTimer;
    let processed = 0;
    let toProcess = 0;
    let percentage = 0;
    let doneButton: HTMLElement

    const cancel = () => {
        dispatch('cancel');
        stopTimer();
        finished = true;
    }
    const close = () => dispatch('close');
    const closeDeselect = () => dispatch('closeDeselect');
    const finish = () => dispatch('finish');

    onMount(() => {
        startTimer();
        toProcess = processor.getCount();

        processor.events.onUpdateCount(count => {
            toProcess = count;
        });

        processor.events.onError(message => {
            percentage = 100;
            finished = true;
            stopTimer();
            error = true;
            errorMessage = message;
        });

        processor.events.onFinish(() => {
            finished = true;
            stopTimer();
            finish();
            setTimeout(() => doneButton?.focus(), 100);
        });

        processor.events.onUpdate((d, ids) => {
            processed = toProcess - ids.length;
            percentage = Math.ceil((processed / toProcess) * 100);
        });
    });

    $: processedFormatted = new Intl.NumberFormat().format(processed);
    $: toProcessFormatted = new Intl.NumberFormat().format(toProcess);
</script>


<div class="acu-flex acu-flex-row-reverse">
	<div>
		<AcTimer bind:start={startTimer} bind:stop={stopTimer}></AcTimer>
	</div>
	<div class="acu-grow">
		{#if error}
			{i18n.failed} <span class="dashicons dashicons-no" style="color: var(--ac-notification-red)"></span>
		{:else if !finished}
			{i18n.bulk_delete.finished.split( '{0}' )[ 0 ]} {processedFormatted}
			/ {toProcessFormatted}
		{:else }
			{i18n.bulk_delete.finished.split( '{0}' )[ 0 ]} {processedFormatted} {i18n.bulk_delete.finished.split( '{0}' )[ 1 ]}
			(100%) <span class="dashicons dashicons-yes"></span>
		{/if}
	</div>
</div>

<AcProgressBar {percentage} {finished} {error}></AcProgressBar>

{#if error}
	<div class="acu-mb-3">
		<em>{errorMessage}</em>
	</div>
{/if}

<div>
	{#if finished}
		<button class="button button-primary" on:click={ close } bind:this={doneButton}>{i18n.done}</button>
		<button class="button" on:click={ closeDeselect }>{i18n.bulk_edit.done_deselect}</button>
	{:else }
		<button class="button" on:click={ cancel }>{i18n.cancel}</button>
	{/if}
</div>
