import Nanobus from "nanobus";
export default class BulkProcessorEvents extends Nanobus {
    updateCount(items) {
        this.emit('updateCount', items);
        return this;
    }
    emitError(message) {
        this.emit('error', message);
    }
    onUpdateCount(cb) {
        this.addListener('updateCount', cb);
        return this;
    }
    finish(column, value) {
        this.emit('finished', column, value);
    }
    onFinish(cb) {
        this.addListener('finished', cb);
        return this;
    }
    onError(cb) {
        this.addListener('error', cb);
        return this;
    }
    update(processed, items) {
        this.emit('update', processed, items);
    }
    onUpdate(cb) {
        this.addListener('update', cb);
        return this;
    }
}
