<script lang="ts">

    import {onMount} from "svelte";
    import {getEditingTranslation} from "ACP/Editing/helpers/translations";
    import {getVarBulkDelete} from "ACP/Editing/helpers/inline-vars";

    export let args: { reassign_user: number } = {};

    declare const ACP_Editing: AcpEditingLocalized;
    declare const ajaxurl: string;

    let type = 2;
    let i18n = getEditingTranslation();
    let bulkDelete = getVarBulkDelete();
    let selectElement;

    const resetUserId = () => {
        if (!args.hasOwnProperty('reassign_user') || args.reassign_user === null) {
            args.reassign_user = bulkDelete.reassign_user_id
        }
    }

    onMount(() => {
        resetUserId();
        setTimeout(() => {
            jQuery(selectElement).ac_select2({
                width: 250,
                theme: 'acs2',
                escapeMarkup: (text: string) => jQuery('<div>' + text + '</div>').text(),
                ajax: {
                    url: ajaxurl,
                    dataType: 'json',
                    delay: 500,
                    data: function (params: any) {
                        return {
                            action: 'acp_editing_request',
                            method: 'delete-user-select-values',
                            searchterm: params.term,
                            page: params.page,
                            _ajax_nonce: AC.ajax_nonce
                        };
                    },
                    processResults: function (response: any) {
                        return response.success ? response.data : {results: []}
                    }
                }
            })

            jQuery(selectElement).on('select2:select', (d) => {
                args.reassign_user = selectElement.value;
                type = 2;
            });
        }, 100);

    });

</script>
<div>
	<p class="acp-bd-description">
		{i18n.bulk_delete.user_assignment_description}
	</p>
	<div class="acp-bd-user-assignment">
		<div>
			<label>
				<input bind:group={type} type="radio" value={2} on:click={resetUserId}/> {i18n.bulk_delete.assignment_to}
			</label>
		</div>
		<div>
			<select bind:this={selectElement}>
				<option value={bulkDelete.reassign_user_id}>{bulkDelete.reassign_user_name}</option>
			</select>
		</div>
	</div>
	<div style="margin-bottom: 5px;">
		<label>
			<input bind:group={type} type="radio" value={1} on:click={()=> args.reassign_user = null }/> {i18n.bulk_delete.delete_all_content}
		</label>
	</div>

</div>